.main-sider {
  background: #753a88;
}
.main-header {
  background: #753a88;
  background-image: -webkit-gradient(linear, right top, left top, from(#cc2b5e), to(#753a88));
}
.main-content {
  position: relative;
  margin: 0px 22px 22px 22px;
  background: #ffffff;
  border-radius: 2px;
  min-height: auto;
}
.no-margin-top {
  margin-top: 0;
}
.main-table .tab-revise-btn {
  cursor: pointer;
  color: #753a88;
}
.main-table .tab-delete-btn {
  cursor: pointer;
  color: #cc2b5e;
}
.main-table .tab-see-btn {
  cursor: pointer;
  color: firebrick;
}
.main-table .ant-table-thead > tr.ant-table-row-hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td,
.main-table .ant-table-tbody > tr.ant-table-row-hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td,
.main-table .ant-table-thead > tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td,
.main-table .ant-table-tbody > tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td {
  background: #f5f5f5;
}
button.ant-modal-close,
button.ant-btn {
  margin-top: 0;
}
.top-btn {
  margin-left: 15px;
}
.home-top-cardbox {
  margin: 20px 20px 50px 20px;
}
.home-top-cardbox .gutter-box {
  background: #f5f5f5;
  height: 200px;
  border-radius: 5px;
  text-align: center;
  padding: 25px 10px 20px 10px;
}
.home-top-cardbox .gutter-box .home-top-icon {
  font-size: 48px;
  display: block;
  color: #ffffff;
}
.home-top-cardbox .gutter-box h5 {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #ffffff;
  margin-top: 10px;
  font-size: 18px;
}
.home-top-cardbox .gutter-box h6 {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #ffffff;
  margin-top: 10px;
  font-size: 32px;
}
.rdw-editor-wrapper .rdw-image-modal-btn,
.rdw-editor-wrapper .rdw-embedded-modal-btn,
.rdw-editor-wrapper .rdw-link-modal-buttonsection {
  line-height: 1;
}
.rdw-editor-wrapper .rdw-embedded-modal,
.rdw-editor-wrapper .rdw-link-modal {
  height: auto !important;
}
.rdw-editor-wrapper .rdw-colorpicker-modal-options {
  overflow: auto;
}
