.logo-icon {
  width: 36px;
  height: 36px;
  background: url(data:image/png;base64,) center center no-repeat;
  background-size: 100% auto;
  display: inline-block;
  vertical-align: middle;
  margin: 0 3px;
  position: relative;
  top: -3px;
}
.left-nav {
  position: relative;
  width: 100%;
  height: 100%;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
}
.left-nav .left-nav-title {
  color: #ffffff;
  font-size: 26px;
  height: 64px;
  line-height: 64px;
  overflow: hidden;
  text-align: center;
}
.left-nav .ant-menu-dark,
.left-nav .ant-menu-dark .ant-menu-sub {
  background: #753a88;
}
.left-nav .ant-menu-dark .ant-menu-inline.ant-menu-sub {
  background: #7d3884;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15) inset;
}
.left-nav .ant-menu.ant-menu-dark .ant-menu-item-selected,
.left-nav .ant-menu-submenu-popup.ant-menu-dark .ant-menu-item-selected {
  background: #8c60a5;
}
.left-nav ::-moz-selection {
  background: transparent;
}
.left-nav ::selection {
  background: transparent;
}
.left-nav .left-nav-menu-box {
  position: absolute;
  top: 64px;
  bottom: 0;
  left: 0;
  overflow-y: auto;
  right: 0;
  width: 100%;
}
.left-nav-close {
  width: 15px;
  height: 70px;
  background: #753a88;
  color: #ffffff;
  position: absolute;
  right: -15px;
  top: 50%;
  margin-top: -10px;
  font-size: 12px;
  cursor: pointer;
  line-height: 70px;
  display: block;
  border-radius: 0 5px 5px 0;
  z-index: 1;
}
.left-nav-popup .ant-menu-sub {
  background: #753a88;
}
