.header-nav {
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-align: right;
  font-size: 14px;
  color: #ffffff;
}
.header-nav h6 {
  color: #ffffff;
  vertical-align: middle;
  margin: 0;
}
.header-nav span {
  margin: 0 22px 0 4px;
}
.header-nav button {
  background: transparent;
  font-size: 14px;
  margin-top: 0;
  padding: 0;
  letter-spacing: 1px;
  color: white;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  border-radius: 0;
  text-shadow: none;
  -webkit-transition: none;
  transition: none;
  position: relative;
}
